.footer {
    max-width: 1280px;
    margin-left: auto;
    margin-right: auto;

    padding-top: 56px;
    padding-bottom: 56px;
    background-color: getColor(secondary-bcg);
}



.footer .copyright {
    font-weight: 600;
    color: getColor(text-price-contacts-color);
}

.footer .link {
    font-weight: 600;
}

.footer .link:hover,
.footer .link:focus {
    color: getColor(accent);
}

.copiright {
    font-weight: 600;
    color: getColor(text-price-contacts-color);
}