.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, .3);
  position: fixed;
  top: 0;
  left: 0;
}

.modal-wrapper {
  width: 500px;
  height: 400px;
  background-color: #ff0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-icon {
  justify-content: center;
  align-items: center;
  display: flex;
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .3);
  transition: opacity .3s cubic-bezier(.175, .885, .32, 1.275), visibility .3s cubic-bezier(.175, .885, .32, 1.275);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal-wrapper {
  width: 500px;
  background-color: #fff;
  border-radius: 20px;
  transition: transform .3s ease-in-out .2s, opacity .3s ease-in-out .2s;
  position: absolute;
  top: 50%;
  left: 50%;
}

.backdrop.is-hidden .modal-wrapper {
  opacity: 0;
  transform: translate(-50%, -50%)scale(.4);
}

.backdrop .modal-wrapper {
  opacity: 1;
  transform: translate(-50%, -50%)scale(1);
}

