.price {
    max-width: 1280px;
    margin-left: auto;
    margin-right: auto;

    background-image: linear-gradient(rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)),
        url('../images/price-overlay.jpg');

    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.price-list {
    display: flex;
    margin-bottom: 50px;
}

.price-list .item {
    padding: 30px 40px;
    width: 585px;
    height: 240px;
    outline: 2px solid getColor(accent);
}

.price .uppertitle {
    display: flex;
    align-items: center;
    font-weight: 600;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: getColor(btn-dis);
}

.price .uppertitle:hover,
.price .uppertitle:focus {
    color: getColor(accent);
}

.price .uppertitle:hover::before,
.price .uppertitle:focus::before {
    background-color: getColor(accent);
}

.price .uppertitle::before {
    content: '';
    margin-right: 20px;
    display: block;
    width: 60px;
    height: 1px;
    background-color: getColor(secondary);
}

.price .section-title {
    margin-bottom: 44px;
    font-weight: 700;
    font-size: 42px;
    letter-spacing: 0.05em;
    color: getColor(secondary);
}

.inner-list {
    font-size: 18px;
    line-height: 1.67;
    color: getColor(text-price-contacts-color);
}

.inner-item:not(:last-child) {
    margin-bottom: 20px;
}

.button-container {
    text-align: center;
}