.gallery .uppertitle {
    display: inline-flex;
    align-items: center;
    margin-bottom: 44px;
    font-weight: 600;
    letter-spacing: 0.1em;
    text-transform: uppercase;
}

.gallery .uppertitle:hover,
.gallery .uppertitle:focus {
    color: getColor(accent);
}

.gallery .uppertitle:hover::before,
.gallery .uppertitle:focus::before {
    background-color: getColor(accent);
}

.gallery .uppertitle::before {
    content: '';
    margin-right: 20px;
    width: 60px;
    height: 1px;
    background-color: getColor(primary);
}

.gallery .section-title {
    font-weight: 700;
    font-size: 42px;
    line-height: 1.16;
    letter-spacing: 0.05em;
}

.gallery .list {
    display: flex;
    flex-wrap: wrap;
    margin: -15px;
}

.gallery .item {
    position: relative;
    margin: 15px;
    flex-basis: calc((100% - 120px) / 4);
}

.gallery .item::before {
    position: absolute;
    bottom: 0;
    left: 0;
    content: '';
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 50px;
    background-color: getColor(gall-bcg);
}